import React, { memo } from 'react';

// Import all section components
import BasicInfo from './BasicInfo';
import HistoriaEscola from './HistoriaEscola';
import PovosLinguas from './PovosLinguas';
import Ensino from './Ensino';
import Infraestrutura from './Infraestrutura';
import GestaoProfessores from './GestaoProfessores';
import HistoriadoProfessor from './HistoriadoProfessor';

// Componente decorativo para separadores indígenas
const IndigenousSeparator = memo(() => (
  <div className="flex items-center justify-center py-6">
    <div className="flex-1 h-px bg-gradient-to-r from-transparent via-amber-400/30 to-transparent"></div>
    <div className="mx-4 flex items-center space-x-2">
      <div className="w-2 h-2 rounded-full bg-amber-500/60"></div>
      <div className="w-1 h-1 rounded-full bg-green-600/60"></div>
      <div className="w-2 h-2 rounded-full bg-amber-500/60"></div>
    </div>
    <div className="flex-1 h-px bg-gradient-to-r from-transparent via-amber-400/30 to-transparent"></div>
  </div>
));

// Componente wrapper para seções fluidas
const FluidSection = memo(({ children, className = '' }) => (
  <section className={`py-6 ${className}`}>
    {children}
  </section>
));

const EscolaInfo = memo(({ escola, shouldUseGrid = false, refreshKey = 0 }) => {
  console.log("EscolaInfo recebeu:", escola);
  
  if (!escola) {
    console.log("EscolaInfo: escola é null ou undefined");
    return null;
  }

  return (
    <div className="space-y-0">
      {/* Informações Básicas */}
      <FluidSection>
        <BasicInfo escola={escola} />
      </FluidSection>

      <IndigenousSeparator />

      {/* Povos e Línguas */}
      <FluidSection>
        <PovosLinguas escola={escola} />
      </FluidSection>

      <IndigenousSeparator />

      {/* Ensino */}
      <FluidSection>
        <Ensino escola={escola} />
      </FluidSection>

      <IndigenousSeparator />

      {/* Infraestrutura */}
      <FluidSection>
        <Infraestrutura escola={escola} />
      </FluidSection>

      <IndigenousSeparator />

      {/* Gestão e Professores */}
      <FluidSection>
        <GestaoProfessores escola={escola} />
      </FluidSection>

      <IndigenousSeparator />

      {/* História da Escola em destaque */}
      <FluidSection className="bg-gradient-to-br from-amber-50/30 to-green-50/30 rounded-2xl mx-4">
        <HistoriaEscola escola={escola} refreshKey={refreshKey} />
      </FluidSection>

      <IndigenousSeparator />

      {/* História do Professor */}
      <FluidSection className="bg-gradient-to-br from-green-50/30 to-amber-50/30 rounded-2xl mx-4">
        <HistoriadoProfessor escola={escola} refreshKey={refreshKey} />
      </FluidSection>
    </div>
  );
});

export default EscolaInfo; 