import React, { memo } from 'react';
import { Sparkles, BookOpen, GraduationCap, Users } from 'lucide-react';
import InfoItem from '../InfoItem';
import BooleanValue from '../BooleanValue';

const Ensino = memo(({ escola }) => {
  if (!escola) return null;

  return (
    <div className="space-y-8">
      {/* Cabeçalho da seção */}
      <div className="text-center space-y-2">
        <h2 className="text-2xl font-bold text-green-900 tracking-wide">
          Ensino e Aprendizagem
        </h2>
        <p className="text-green-700 text-sm">
          Modalidades e materiais pedagógicos
        </p>
      </div>

      {/* Modalidades de Ensino */}
      <div className="space-y-4">
        <div className="flex items-center gap-3 text-green-800">
          <GraduationCap className="w-5 h-5" />
          <h3 className="text-lg font-semibold">Modalidades</h3>
        </div>
        
        <div className="space-y-3 pl-8">
          <InfoItem label="Modalidade de Ensino" value={escola.modalidade_ensino} />
          <InfoItem label="Número de Alunos" value={escola.numero_alunos} />
          {escola.linguas_faladas && (
            <InfoItem label="Línguas Faladas" value={escola.linguas_faladas} />
          )}
        </div>
      </div>

      {/* Materiais Pedagógicos */}
      <div className="space-y-4 pt-6 border-t border-green-200/50">
        <div className="flex items-center gap-3 text-green-800">
          <BookOpen className="w-5 h-5" />
          <h3 className="text-lg font-semibold">Materiais Pedagógicos</h3>
        </div>
        
        <div className="pl-8 space-y-2">
          <p className="text-green-700 text-sm italic">
            Diferenciados e não diferenciados, produzidos dentro e fora da comunidade.
          </p>
          
          <div className="space-y-3 pt-2">
            <InfoItem 
              label="Material Pedagógico Não Indígena" 
              value={<BooleanValue value={escola.material_nao_indigena} />} 
            />
            <InfoItem 
              label="Material Pedagógico Indígena" 
              value={<BooleanValue value={escola.material_indigena} />} 
            />
          </div>
        </div>
      </div>
    </div>
  );
});

export default Ensino; 