import React, { memo } from 'react';
import { BookOpen } from 'lucide-react';
import ImagemHistoriadoProfessor from '../ImagemHistoriadoProfessor';

const HistoriadoProfessor = memo(({ escola, refreshKey = 0 }) => {
  const historia = escola?.historia_do_prof;
  if (!historia) return null;

  return (
    <div className="space-y-8">
      {/* Cabeçalho da seção */}
      <div className="text-center space-y-4">
        <div className="flex items-center justify-center gap-4">
          <span className="bg-green-100 rounded-full flex items-center justify-center w-16 h-16">
            <img 
              src={process.env.PUBLIC_URL + '/passaro.svg'} 
              alt="Ícone de pássaro" 
              className="w-12 h-12" 
              style={{ filter: 'none', borderRadius: '0', boxShadow: 'none' }}
              aria-hidden="true"
            />
          </span>
          <h2 className="text-2xl font-bold text-green-900 tracking-wide">
            História dos Professores
          </h2>
        </div>
        <p className="text-green-700 text-sm">
          Memória e trajetória dos educadores
        </p>
      </div>

      {/* Conteúdo da história */}
      <div className="prose prose-sm sm:prose-base md:prose-lg lg:prose-xl max-w-none
                      prose-headings:text-green-900 
                      prose-p:text-green-800 
                      prose-p:leading-relaxed 
                      prose-p:text-justify
                      prose-a:text-green-700 
                      prose-a:no-underline 
                      prose-a:border-b-2 
                      prose-a:border-green-100 
                      hover:prose-a:border-green-600">
        <div className="text-base sm:text-lg leading-relaxed text-neutral-800">
          {historia}
        </div>
      </div>

      <ImagemHistoriadoProfessor escola_id={escola.id} refreshKey={refreshKey} />
    </div>
  );
});

export default HistoriadoProfessor;
