import React, { memo } from 'react';
import { Heart, Users, Languages } from 'lucide-react';
import InfoItem from '../InfoItem';

const PovosLinguas = memo(({ escola }) => {
  if (!escola) return null;

  return (
    <div className="space-y-6">
      {/* Cabeçalho da seção */}
      <div className="text-center space-y-2">
        <h2 className="text-2xl font-bold text-green-900 tracking-wide">
          Povos e Línguas
        </h2>
        <p className="text-green-700 text-sm">
          Identidade cultural e linguística
        </p>
      </div>

      {/* Povos Indígenas */}
      <div className="space-y-4">
        <div className="flex items-center gap-3 text-green-800">
          <Users className="w-5 h-5" />
          <h3 className="text-lg font-semibold">Povos Indígenas</h3>
        </div>
        
        <div className="pl-8">
          <InfoItem 
            label="Povos Indígenas" 
            value={escola.povos_indigenas} 
            className="mb-2"
          />
        </div>
      </div>

      {/* Línguas (se houver dados) */}
      {escola.linguas_faladas && (
        <div className="space-y-4 pt-6 border-t border-green-200/50">
          <div className="flex items-center gap-3 text-green-800">
            <Languages className="w-5 h-5" />
            <h3 className="text-lg font-semibold">Línguas Faladas</h3>
          </div>
          
          <div className="pl-8">
            <InfoItem 
              label="Línguas" 
              value={escola.linguas_faladas} 
            />
          </div>
        </div>
      )}
    </div>
  );
});

export default PovosLinguas; 